.content-container {
  min-height: calc(100vh - 145px);
}

.text-red {
  color: $error-color;
}

.text-green {
  color: $success-color;
}

.pointer {
  cursor: pointer;
}

.custom-badge {
  background-color: $gray-darker;
  color: $background-color-secondary;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  padding: 3px 5px;
  width: fit-content;
  font-size: 10px;
}
.custom-badge,
a {
  text-decoration: none !important;
}

textarea {
  resize: vertical;
}

.input-field {
  font-size: 12px;
  padding: 5px 10px;
  text-transform: unset;
  border-radius: 3px;
  border: 1px solid $background-color-ternary;
  box-shadow: none !important;
}

.break-word {
  white-space: initial;
  word-wrap: break-word;
  word-wrap: break-word;
  /* width: 150px; */
}

.summation-box {
  display: flex;
  align-items: center;
  margin-top: 10px;
  .numbers {
    background-color: $background-color-ternary;
    border-color: $background-color-ternary;
    width: 130px;
    padding: 10px;
    margin: 10px 0px;
    text-align: center;
    font-size: 14px;
  }
  .symbol {
    padding: 0px 10px;
  }
  .sum-input {
    width: 130px;
    text-transform: unset;
    height: 40px;
    border-radius: 3px;
    border: 1px solid $background-color-ternary;
    box-shadow: none !important;
    text-align: center;
  }
}

.line-through-text {
  text-decoration: line-through;
}
.error-text {
  color: $error-color !important;
  font-size: 10px !important;
}

.success-color {
  color: $success-color;
}

.error-color {
  color: $error-color;
}
.no-orders {
  font-size: 14px;
  padding: 20px 0px 5px 15px;
}

.cancelcart-btn {
  margin-top: 10px !important;
  background-color: red !important;
  padding: 1px 5px !important;
}

.comment-color {
  color: red;
}

.cursor-pointer {
  cursor: pointer;
}

.wordwrap {
  white-space: pre-line !important;
  word-wrap: break-word;
}

.change-order-dropdown {
  padding: 0px 10px;
  font-size: 12px;
  text-transform: unset;
  height: 35px;
  border-radius: 3px;
  border: 1px solid $background-color-ternary;
  box-shadow: none !important;
  margin-bottom: 10px;
  .statuses {
    padding: 10px !important;
  }
}

.Toastify {
  .Toastify__toast-container {
    .Toastify__toast--error {
      background: rgba(255, 87, 34, 0.9);
    }
    .Toastify__toast--success {
      background: rgba(61, 34, 119, 0.8);
    }
    .Toastify__toast--warning {
      background: rgba(155, 87, 34, 0.9);
    }
    .Toastify__toast-body {
      font-size: 13px;
      font-weight: 600;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    }
  }
}
.tickets-container {
  display: flex;
  flex-direction: column;
  .custom-badge {
    margin: 5px;
  }
}
